* {
  margin: 0;
  padding: 0;
}

#wrapper {
  font-family: sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#containerHeader {
  display: flex;
  width: 100%;
  height: 75px;
  justify-content: space-between;
  background-image: linear-gradient(to top, #cfd4df 0%, #e2ebf0 100%);
  box-shadow: 0px 0px 20px 0px black;
  align-items: center;
}
#containerHeader div {
  margin: 20px;
  display: flex;
  align-items: center;
}
#containerHeader div span {
  color: rgb(0, 153, 255);
  font-size: 1.7rem;
}
#containerHeader div img {
  width: 20px;
}
#containerHeader div ul {
  list-style: none;
  display: flex;
  gap: 10px;
  margin-left: 30px;
}
#containerHeader div ul li a {
  color: rgb(0, 0, 0);
  text-decoration: none;
  padding: 5px;
}
#containerHeader div ul li a:hover {
  border-bottom: 1px solid gray;
}

#seccionInfo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 75px;
  background-color: rgb(245, 245, 245);
  width: 100%;
}
#articuloInformacion {
  display: flex;
  width: 90%;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  gap: 55px;
  padding: 40px 0px 20px 0px;
}
#contenedorFotoYtexto {
  display: flex;
  width: 90%;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  gap: 40px;
  text-align: center;
}
#contenedorFotoYtexto div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 500px;
  gap: 30px;
}
#contenedorFotoYtexto div h2 {
  font-weight: 100;
  font-size: 3.5rem;
  color: rgb(0, 153, 255);
}
#contenedorFotoYtexto div h2 {
  font-weight: 100;
  font-size: 3rem;
  color: rgb(0, 153, 255);
}
#botonesContactar {
  margin-top: 20px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  width: 100%;
  justify-content: space-evenly;
}
#botonesContactar a {
  opacity: 20%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 3px;
}
#botonesContactar a:hover {
  opacity: 100%;
  box-shadow: 0px 0px 4px gray;
  border-radius: 100%;
}
#botonesContactar a img {
  width: 40px;
}
#fotoPersonal {
  width: 40%;
  max-width: 250px;
  min-width: 200px;
  border-radius: 50%;
  box-shadow: 0px 0px 50px 0px rgb(0, 153, 255);
}

#seccionHabilidades {
  margin-top: 75px;
  background-color: rgb(245, 245, 245);
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 50px;
}
#seccionHabilidades h2 {
  margin: 30px;
  font-weight: 100;
  font-size: 2rem;
  border-bottom: 1px solid lightgray;
  width: 100%;
  text-align: center;
  padding-bottom: 10px;
}
#seccionHabilidades article {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 45px;
  justify-content: center;
  padding: 20px;
}

#lenguajeProgramacion {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 15px;
  box-shadow: 0px 0px 5px 0px lightgray;
  border-radius: 10px;
}
#lenguajeProgramacion img {
  border-bottom: 1px solid lightgray;
}
#lenguajeProgramacion p {
  color: lightgray;
}

#lenguajeProgramacion:hover {
  box-shadow: 0px 0px 5px 0px rgb(0, 153, 255);
  color: black;
}

#lenguajeProgramacion:hover p {
  color: black;
}
